@use 'base/variables' as v;

.no-webp .imagen-contacto {
    background-image: url("#{v.$img_path}/encuentra.jpg");
}

.webp .imagen-contacto {
    background-image: url("#{v.$img_path}/encuentra.webp");
}

.imagen-contacto {
    background-position: center center;
    background-size: cover;
    padding: calc(v.$separacion * 2) 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    h2 {
        font-size: 4rem;
        font-weight: v.$black;
        color: v.$blanco;
    }

    p {
        font-size: 1.8rem;
        color: v.$blanco;
    }
}